import 'viewerjs/dist/viewer.css'
import Viewer from 'v-viewer'
import Vue from 'vue'

Vue.use(Viewer)

import chaobject from "@/components/script/chaobject";

export default {
  name: "cha_images",
  mixins: [chaobject],
  props: {
    cha_data: undefined
  },
  computed: {},
  created() {
    if (this.cha_data && this.cha_data.images) {
      for (let i = 0; i < this.cha_data.images.length; ++i) {
        var image_url = this.generate_image_url(this.cha_data.images[i], 'i');
        this.images.push(image_url);
      }
    }
  },
  data() {
    return {
      images: []
    }
  },
}
