import cha_buy from "@/components/cha/item/cha_buy";
import chaobject from "@/components/script/chaobject";

export default {
  name: "cha_show",
  mixins: [chaobject],
  data() {
    return {
      loading: false,
      contact_info: undefined
    }
  },
  props: {
    cha_data: undefined
  },
  components: {
    cha_buy
  },
  created() {
    if (this.$store.state.hasLogin && this.$store.state.user.userinfo.is_vip) {

    }
  },
  methods: {
    on_contact_info: function (contact_info) {
      console.log('on_contact_info:', contact_info);
      this.contact_info = contact_info;
      this.$forceUpdate();
    },
    on_get_contact: function () {
      this.loading = true;
      this.$axios.get('/app/cha/contact', {
        params: {
          'id': this.cha_data.id
        }
      }).then((response) => {
        console.log('[get_contact]response:', response);
        this.contact_info = response.data.contact_info;
        this.loading = false;

      }).catch((error) => {
        this.loading = false;
        console.log('error:', error);
      });
    }
  }
}
