export default {
  name: "cha_tab",
  components: {
    cha_report_dialog: () => import('@/components/cha/item/cha_report_dialog')
  },
  props: {
    cha_data: undefined
  },
  computed: {
    is_manager: function () {
      try {
        return this.$store.state.user.userinfo.is_manager
      } catch (e) {

      }
      return false;
    }
  },
  data: function () {
    return {
      show_reqport_dialog: false,
      defalut_tabinfo: {
        is_like: false,
        is_dislike: false,
        is_star: false,
        like_count: 0
      }
      ,
      tabinfo: Object.assign({}, this.defalut_tabinfo),
      loaded_tabinfo: false
    }
  },
  created() {
    this.load_tabinfo();
  },
  methods: {
    load_tabinfo: function () {
      /**if (!this.$store.state.hasLogin) {
        this.loaded_tabinfo = true;
        return;
      }*/
      console.log('call load_tabinfo:', this.tabinfo);
      this.$axios.get('/app/cha/tabinfo', {
        params: {
          'id': this.cha_data.id
        }
      }).then((response) => {
        this.loaded_tabinfo = true;
        if (response.data.tabinfo) {
          this.tabinfo = response.data.tabinfo;
        }
      }).catch((error) => {

      });
    },
    on_video_like: function () {
      if (!this.$store.state.hasLogin) {
        return this.$app._userbar.show_login_dialog();
      }
      var post_data = {
        'action': !this.tabinfo.is_like,
        'hash_id': this.online_video.hash_id,
      }
      console.log('on_video_like:', post_data);
      this.$axios.post('/app/video/like', post_data).then((response) => {
        this.tabinfo.is_like = true;
        this.tabinfo.is_dislike = false;
        this.$forceUpdate();
      }).catch((error) => {
        console.log("error:", error);
      });
    },
    on_video_dislike: function () {
      if (!this.$store.state.hasLogin) {
        return this.$app._userbar.show_login_dialog();
      }
      var post_data = {
        'action': !this.tabinfo.is_dislike,
        'hash_id': this.online_video.hash_id
      }
      console.log('on_video_like:', post_data);
      this.$axios.post('/app/video/dislike', post_data).then((response) => {
        this.tabinfo.is_like = true;
        this.tabinfo.is_dislike = false;
      }).catch((error) => {
        console.log("error:", error);
      });
    },
    on_report: function () {
      if (!this.$store.state.hasLogin) {
        return this.show_no_login_dialog();
      }
      this.show_reqport_dialog = false;
      this.$nextTick(() => {
        this.show_reqport_dialog = true;
      });
    },
    on_star: function () {
      if (!this.$store.state.hasLogin) {
        return this.show_no_login_dialog();
      }
      var post_data = {
        'action': !this.tabinfo.is_star,
        'id': this.cha_data.id,
      }
      this.$axios.post('/app/cha/star', post_data).then((response) => {
        this.tabinfo.is_star = !this.tabinfo.is_star;
        this.$forceUpdate();
      }).catch((error) => {
        console.log("error:", error);
      });
    }
  }
}
