import chaobject from "@/components/script/chaobject";

export default {
  name: "cha_buy",
  mixins: [chaobject],
  props: {
    cha_data: undefined
  },
  data: function () {
    return {
      buy_gold_num: 20,
      buy_loading: false,
      is_check: false
    }
  },
  created() {
    this.check_is_buy();
  },
  methods: {
    check_is_buy: function () {
      if (!this.$store.state.hasLogin) {
        return this.is_check = true;
      }
      this.$axios.get('/app/cha/buy', {
        params: {
          id: this.cha_data.id,
        }
      }).then((response) => {
        console.log('[check_is_buy]response:', response);
        var contact_info = response.data.contact_info;
        this.$emit("on_contact_info", contact_info);
        this.is_check = true;
      }).catch(function (error) {

      });
    },
    on_buy: function () {
      if (!this.$store.state.hasLogin) {
        return this.show_no_login_dialog("尚未登录，不能购买", false);
      }
      this.buy_loading = true;
      this.buy({id: this.cha_data.id, cb: this.on_buy_callback});
    },
    on_buy_callback: function (response) {
      console.log('on_buy_callback:', response);
      this.buy_loading = false;
      if (response.code == 200) {
        var contact_info = response.data.contact_info;
        this.$emit("on_contact_info", contact_info);
      }
      if (response.code == 501) {

      }
    }
  }
}
