import chaobject from "@/components/script/chaobject";
import cha_images from "@/components/cha/item/cha_images";
import cha_tab from "@/components/cha/item/cha_tab";
import cha_show from "@/components/cha/item/cha_show";
import cha_buy from "@/components/cha/item/cha_buy";

export default {
  name: "view",
  mixins: [chaobject],
  components: {
    cha_images,
    cha_tab,
    cha_show,
    cha_buy
  },
  data() {
    return {
      start: 0,
      limit: this._g.PER_PAGE_COUNT,
      cha_data: undefined,
      province_latest: undefined
    }
  },
  created() {
      this.load_data();
      window._cha_view = this;
  },
  methods: {
    load_data: function () {
      var cid = this.$route.params.id;
      this.get_cha({id: cid, province_latest: true, cb: this.on_thread,error_cb:this.on_error_thread});
    },
    on_thread: function (thread, province_latest) {
      this.cha_data = thread;
      console.log("cha_data:", this.cha_data);
      this.province_latest = province_latest;
      this.update_thread();
    },
    on_error_thread:function (){

    },
    update_thread: function () {
      this.set_title(this.cha_data.title);
    }
  },
  watch: {
    '$route'(to, from) {
      this.cha_data = undefined;
      this.$nextTick(this.load_data);
    }
  },
}
